@import url(https://fonts.googleapis.com/css2?family=ZCOOL+KuaiLe&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic+Coding&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-family: 'Nanum Gothic Coding', monospace;
    -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.recipe_recipe__iQSBG{
    border-radius: 50px;
    box-shadow: 1px 2px 10px rgb(99, 97, 97);
    margin: 15px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background: rgb(255, 255, 255, 0.3);
    align-items: center;
    min-width: 40%;
    width: 55vw;
    height: 100%;
}

img{
    width: 40%;
    height: 40%;
    padding-bottom: 2%;
}

.recipe_calories__OjF-q{
    text-align: center;
}

ul{
    width: 45vw;  
    margin-left: -3%;
}

h2{
    width: 45vw;
    text-align: center;
    font-family: 'ZCOOL KuaiLe', cursive;
}
.App{
min-height: 100vh;
background-image: url('https://cdn.pixabay.com/photo/2019/04/02/04/32/masala-4096891_1280.jpg');
background-repeat: no-repeat;
transition: 0.5s ease;
background-size: cover;

}

.no-bg{
  background-color: rgb(233, 245, 255);
  height: -webkit-max-content;
  height: max-content;
}

.form{
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: right;
  
}

.search-bar{
  font-family: 'Nanum Gothic Coding', monospace;
  width: 25%;
  height: 4vh;
  padding: 10px;
  outline: none;
  border-radius: 6px;
  border: 2px solid rgba(181, 221, 253, 0.5);
}

.title{
  font-size: calc(16px + 1.5vw);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90%;
  width: 100%;
  font-family: 'Raleway', sans-serif;  
  color: black;
  font-weight: 600;
  position: absolute;
  margin: auto;
  padding: auto;
}
button{
  height: 50px;
  border: 2px solid rgba(181, 221, 253, 0.5);
  border-radius: 5px;
  padding: 10px;
  margin: 4px;
  margin-right: 5%;
  font-family: 'Raleway', sans-serif;  
  font-weight: 600;
  background-color: rgb(224, 212, 212);
}

.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}


.no-title{
  display: none;

}
